}
.room-button.ready {
    background-color: #C62828;
    color: #fafafa;
    font-weight: bold;
}
.room-button.waiting {
    background-color: #2E7D32;
    color: #fafafa;
}

.room-selected-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: .8em auto;
    grid-gap: .5em;
    padding: 1em;
}

.room-selected-help {
