@media (max-width: 800px) and (min-width: 550px) {
    .room-spells-list {
        grid-template-columns: 6em 6em;
    }
}

.room-spell-container {
    cursor: pointer;
    position: relative;

    display: grid;
    grid-template-rows: 2em 1fr;
    
    border: 1px solid #212121;
    height: 7em;
    width: 6em;
    margin: calc(.3em + 1px);

    -webkit-transition: 300ms all;

    -o-transition: 300ms all;

    transition: 300ms all;
}
.room-spell-container.small {
    grid-template-rows: .8em 1fr;
    height: 100%;
    width: 100%;
    margin: 0;
}
.room-spell-container.selected {
    background-color: #E65100;
}
.room-spell-container.focus {
    text-decoration: underline;
    border: 2px solid #212121;
    margin: .3em;
}

.room-spell-container:hover {
    background-color: #ddd;
}
.room-spell-container.small:hover {
    background-color: rgba(230, 81, 0, .3);
}
.room-spell-container.small.selected:hover {
